import Vue from 'vue'
import Router from 'vue-router'

const BackendHome = () => import('@/pages/backend-home')
const BackendLogin = () => import('@/pages/backend-login')
const BackendDetailsAdd = () => import('@/pages/backend-details-add')

const FrontArticleTabs = () => import('@/pages/front-article-tabs')
const FrontUserCenter = () => import('@/pages/front-user-center')
const FrontUserAddress = () => import('@/pages/front-user-address')
const FrontChoseAddress = () => import('@/pages/front-chose-address')


const Cart = () => import('@/pages/cart')
const Home = () => import('@/pages/home')

import cookies from "js-cookie";

const inBrowser = typeof window !== 'undefined'

Vue.use(Router)


const guardRoute = (to, from, next) => {
    var token = cookies.get('b_user') || !inBrowser
    if (!token) {
        next('/backend/login')
    } else {
        next()
    }
}

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/backend/',
      name: '',
      component: BackendLogin
    },
    {
      path: '/backend/login',
      name: '',
      component: BackendLogin
    },
    {
      path: '/backend/home',
      name: '',
      component: BackendHome,
      beforeEnter: guardRoute 
    },
    {
      path: '/backend/details',
      name: '',
      component: BackendDetailsAdd,
      beforeEnter: guardRoute 
    },
    {
      path: '/',
      name: '',
      component: Home
    },
    {
      path: '/article/:id',
      name: '',
      component: FrontArticleTabs
    },
    {
      path: '/user',
      name: '',
      component: FrontUserCenter
    },
    {
      path: '/user/address',
      name: '',
      component: FrontUserAddress
    },
    {
      path: '/user/address/select',
      name: '',
      component: FrontChoseAddress
    },
    {
      path: '/cart',
      name: '',
      component: Cart
    }
  ]
})



